import { prefix } from "../constants";
import { PanelBody } from "@wordpress/components";
import { isPro } from "../helpers";

const Upgrader = ({
	text = "Get Pro",
	description = "Enhance your site using our pro blocks!",
}) => {
	if (isPro()) {
		return "";
	}

	return (
		<PanelBody initialOpen={true} className={`${prefix}-upgrader-wrapper`}>
			<div className={`${prefix}-upgrader-button`}>
				<p dangerouslySetInnerHTML={{ __html: description }} />
				{text && (
					<a
						href="https://wcbooster.com/pricing/"
						target="_blank"
						rel="noopener noreferrer"
					>
						<i className="fa-solid fa-arrow-up-from-bracket"></i>
						{text}
					</a>
				)}
			</div>
		</PanelBody>
	);
};

export default Upgrader;
