import {
    isUndefined,
    getResponsiveRangeVal,
    wordTrim,
} from "../../../helpers";

import { prefix } from "../../../constants";

const ProductReviewItemLayoutOne = (props) => { 
    const {
    attributes: {
        enableTitle,
        enableAuthor,
        enableDate,
        enableComment,
        enableRating,
        imageRadius,
        imageSize,
        imageType,
        ratingPosition,
        excerptLength,
    },
    post,
    selectedReview,
    authorStyle,
    dateStyle,
    commentStyle,
    titleStyle,
    ratingStyle,
    productReviewBodyStyle,
    wrapperStyle,
    productImageStyle,
    getImgUrl
    } = props;

    return (
        <div
            className={`${prefix}-product-review-card-wrapper ${prefix}-product-review-layout-one woocommerce`}
            style={wrapperStyle}
        >
            <div className={`${prefix}-product-review-inner-wrapper`} style={productReviewBodyStyle}>
                {imageType === 'reviewer' ? (
                    <div
                        className={`${prefix}-product-review-avatar`}
                        dangerouslySetInnerHTML={{
                            __html: selectedReview.review_image.replace('<img', `
                                <img
                                    style="border-radius: ${imageRadius}%;
                                           height: ${getResponsiveRangeVal(imageSize)};
                                           width: ${getResponsiveRangeVal(imageSize)};"
                                `
                            )
                        }}
                    />
                ) : (
                    getImgUrl(post) && (
                        <div className={`${prefix}-product-review-product-img`}>
                            <img src={getImgUrl(post)} style={productImageStyle} alt="Featured Image" />
                        </div>
                    )
                )}
                { ratingPosition === 'top' && enableRating && (
                    <div
                        style={ratingStyle}
                        className={`${prefix}-product-review-rating`}
                        dangerouslySetInnerHTML={{
                            __html: selectedReview.rating,
                        }}
                    />
                )}
                <div className={`${prefix}-product-review-meta`}>
                    {enableTitle && (
                        <div className={`${prefix}-product-review-title`} style={titleStyle}>
                            {post.title.rendered}
                        </div>
                    )}
                    <div className={`${prefix}-product-review-date-author`}>
                        {enableAuthor && (
                            <span className={`${prefix}-review-author`} style={authorStyle}>
                                {selectedReview.author_name}
                            </span>
                        )}
                        {enableDate && (
                            <span className={`${prefix}-product-review-date`} style={dateStyle}>
                                {selectedReview.date}
                            </span>
                        )}
                    </div>
                    {enableComment && (
                        <div className={`${prefix}-product-review-comment-text`} style={commentStyle}>
                            {wordTrim(selectedReview.comment_text, excerptLength)}
                        </div>
                    )}
                    { ratingPosition === 'bottom' && enableRating && (
                        <div
                            style={ratingStyle}
                            className={`${prefix}-product-review-rating`}
                            dangerouslySetInnerHTML={{
                                __html: selectedReview.rating,
                            }}
                        />
                    )}
                </div>
            </div>
        </div>
    );
};

export default ProductReviewItemLayoutOne;
