import { prefix } from '../constants';

const layoutsIcons = ({
  options = [],
  label,
  activeLayout,
  onChange = e => {},
}) => (
  <div className='components-base-control'>
    {label && <label className='components-base-control__label'>{label}</label>}

    <div className={`${prefix}-button-layout`}>
      {options.map((v, i) => {
        let { name, icon } = v;
        return (
          <button
            key={i}
            onClick={e => {
              onChange(name);
            }}
            className={`${name == activeLayout ? 'active' : ''}`}
            dangerouslySetInnerHTML={{ __html: icon }}
          ></button>
        );
      })}
    </div>
  </div>
);

export default layoutsIcons;
