import { __ } from "@wordpress/i18n";
import {
  InspectorControls,
  InspectorAdvancedControls,
} from "@wordpress/block-editor";
import {
  PanelBody,
  ColorIndicator,
  RangeControl,
  SelectControl,
  ToggleControl,
  __experimentalToggleGroupControl as ToggleGroupControl,
  __experimentalToggleGroupControlOption as ToggleGroupControlOption,
} from "@wordpress/components";
import { createHooks } from "@wordpress/hooks";
import { withSelect } from "@wordpress/data";
import Repeater from "../../../custom-control/repeater";

import {
  SwitchControl,
  PanelColor,
  ColorControl,
  RadiusControl,
  ExcerptLengthControl
} from "../../../custom-control";

import { prefix } from "../../../constants";

import Typography from "../../../custom-control/typography";
import { ImageSizeControl,  } from "../../../custom-control";
import Tab from "../../../components/tab";
import Dimension from "../../../custom-control/dimension";

import metadata from "../block.json";
import Upgrader from "../../../components/upgrader";

import Exporter from "../../../components/exporter";
import { withInstanceId, compose } from "@wordpress/compose";
import { getDefaultAttr, pickBy, isUndefined, wordTrim, getResponsiveRangeVal } from "../../../helpers";
import ResponsiveRangeWrapperControl from "../../../custom-control/responsive-range-wrapper";
import LayoutsIcons from "../../../components/layouts-icon";
import svg from '../../../helpers/svg';

const InspectorPanel = (props) => {
  const {
    attributes: {
      titleTypo,
      authorTypo,
      dateTypo,
      commentTypo,
      ratingTypo,
      enableTitle,
      enableAuthor,
      enableDate,
      enableComment,
      enableRating,
      imageSize,
      authorColor,
      titleColor,
      dateColor,
      commentColor,
      ratingColor,
      arrowRightBgColor,
      arrowRightColor,
      arrowLeftBgColor,
      arrowLeftColor,
      slidesToShow,
      arrowRadius,
      arrowWidth,
      arrowHeight,
      imageRadius,
      autoplay,
      pauseOnHover,
      enableArrow,
      enableDots,
      dotColor,
      arrowPosition,
      contentWidth,
      product_id,
      imageType,
      bgColor,
      titleMargin,
      commentMargin,
      itemMargin,
      ratingMargin,
      contentPadding,
      ratingPosition,
      excerptLength,
      itemBoxRadius,
      layout
    },
    setAttributes,
    clientId,
    allProduct
  } = props;

  const def = getDefaultAttr(metadata);

  if (!allProduct) return null;

  let options = allProduct.map((v) => {
      return {
          label: v.title.raw + (v.wc_booster_product_reviews.length > 0 ? ` (${v.wc_booster_product_reviews.length})` : ' (0)'),
          value: v.id,
      };
  });

  options = [
      {
          label: __("Select a product", "wc-booster"),
          value: 0,
      },
      ...options,
  ];

  // Define a function to generate review options
  const getReviewOptions = (productId) => {
      let review_options = [];
      if (productId && productId !== "0") {
          const index = allProduct.findIndex(product => product.id === parseInt(productId));
          if (index !== -1) {
              const product = allProduct[index];
              review_options = product.wc_booster_product_reviews.map((review, index) => ({
                  label: wordTrim(review.comment_text, 4),
                  value: index + 1,
              }));
          }
      }

      review_options = [
          {
              label: __("Select a review comment", "wc-booster"),
              value: 0,
              reviewsCount: 0,
          },
          ...review_options,
      ];

      return review_options;
  };

  const arrowRightData = {
    arrowRightBgColor: {
      value: arrowRightBgColor,
      title: __("Bg", "wc-booster"),
    },
    arrowRightColor: {
      value: arrowRightColor,
      title: __("Arrow", "wc-booster"),
    },
  };

  const arrowRightSetting = Object.keys(arrowRightData).map((key) => {
    const value = arrowRightData[key].value;
    return {
      title: () => (
        <div>
          {arrowRightData[key].title}
          <ColorIndicator colorValue={value} />
        </div>
      ),
      content: () => (
        <ColorControl
          value={value}
          hideIndicator={true}
          onChange={(c) => setAttributes({ [key]: c ? c : "transparent" })}
        />
      ),
    };
  });

  const arrowLeftData = {
    arrowLeftBgColor: {
      value: arrowLeftBgColor,
      title: __("Bg", "wc-booster"),
    },
    arrowLeftColor: {
      value: arrowLeftColor,
      title: __("Arrow", "wc-booster"),
    },
  };

  const arrowLeftSetting = Object.keys(arrowLeftData).map((key) => {
    const value = arrowLeftData[key].value;
    return {
      title: () => (
        <div>
          {arrowLeftData[key].title}
          <ColorIndicator colorValue={value} />
        </div>
      ),
      content: () => (
        <ColorControl
          value={value}
          hideIndicator={true}
          onChange={(c) => setAttributes({ [key]: c ? c : "transparent" })}
        />
      ),
    };

  });

  const { circle, rectangle } = svg.profileCardPro;

  const collageBoxLayout = {
    active: 'one',
    layouts: [
      { name: 'one', icon: circle },
      { name: 'two', icon: rectangle }
      ]
  };

  return (
    <>
      <InspectorAdvancedControls>
        <Exporter clientId={clientId} />
      </InspectorAdvancedControls>
      <InspectorControls>
        <Upgrader 
        description="Get access to Product Review block for product single page with our Pro version!"
        />
        <PanelBody title={__("General", "wc-booster")} initialOpen={true}>
          <Repeater
            value={product_id}
            limit={wp.hooks.applyFilters("wcBoosterProductReviewLimit", 5)}
            onChange={(product_id, key) => {
              if (key === "id") {
                let data = JSON.parse(product_id);
                product_id = JSON.stringify(data);
              }
              setAttributes({ product_id });
            }}
            cb={($this, p, i) => {
              const review_options = getReviewOptions(p.id);
              return (
                <>
                  <SelectControl
                    value={p.id ? p.id : 0}
                    options={options}
                    onChange={$this.handleChange(i, "id")}
                  />
                  {p.id && (
                    <SelectControl
                      value={p.review_id ? p.review_id : 0}
                      options={review_options}
                      onChange={$this.handleChange(i, "review_id")}
                    />
                  )}
                </>
              );
            }}
          />
          <hr />
          <LayoutsIcons
            options={collageBoxLayout.layouts}
            activeLayout={layout}
            label={__("Layouts", "wc-booster")}
            onChange={(newLayout) => setAttributes({ layout: newLayout })}
          />
        </PanelBody>

        <PanelBody title={__("Slider", "wc-booster")} initialOpen={false}>
          <ResponsiveRangeWrapperControl
            defaultValue={metadata.attributes.slidesToShow.default}
            value={slidesToShow}
            onChange={(slidesToShow) => {
              setAttributes({ slidesToShow });
            }}
            label={__("Slides To Show", "wc-booster")}
            beforeIcon=""
          />

          {allProduct.length > getResponsiveRangeVal(slidesToShow) && (
            <>
              <SwitchControl
                label={__("Auto Play", "wc-booster")}
                checked={autoplay}
                onChange={(autoplay) => {
                  setAttributes({ autoplay });
                }}
              />

              {autoplay && (
                <SwitchControl
                  label={__("Pause On Hover", "wc-booster")}
                  checked={pauseOnHover}
                  onChange={(pauseOnHover) => {
                    setAttributes({ pauseOnHover });
                  }}
                />
              )}

              <SwitchControl
                label={__("Enable Arrow", "wc-booster")}
                checked={enableArrow}
                onChange={(enableArrow) => {
                  setAttributes({ enableArrow });
                }}
              />

              <SwitchControl
                label={__("Enable Dots", "wc-booster")}
                checked={enableDots}
                onChange={(enableDots) => {
                  setAttributes({ enableDots });
                }}
              />
            </>
          )}
        </PanelBody>

        <PanelBody title={__("Content", "wc-booster")} initialOpen={false}>

            <RadiusControl
              label={__("Content Box Radius", "wc-booster")}
              value={itemBoxRadius}
              initialPosition={def("itemBoxRadius")}
              defaultValue={def("itemBoxRadius")}
              onChange={(itemBoxRadius) => setAttributes({ itemBoxRadius })}
            />
          <ToggleGroupControl
            label={__("Rating Position", "wc-booster")}
            value={ratingPosition} 
            onChange={(ratingPosition) => setAttributes({ ratingPosition })}
            isBlock
            >
            <ToggleGroupControlOption value="top" label={__("Top", "wc-booster")} />
            <ToggleGroupControlOption value="bottom" label={__("Bottom", "wc-booster")} />
          </ToggleGroupControl>
          <hr />
          <ResponsiveRangeWrapperControl
            label={__("Width", "wc-booster")}
            defaultValue={def("contentWidth")}
            value={contentWidth}
            onChange={(contentWidth) => setAttributes({ contentWidth })}
            beforeIcon=""
          />

          {enableComment && (
            <ExcerptLengthControl
              value={excerptLength}
              onChange={(excerptLength) => setAttributes({ excerptLength })}
            />
          )}

          <SwitchControl
            label={__("Enable Title", "wc-booster")}
            checked={enableTitle}
            onChange={(enableTitle) => {
              setAttributes({ enableTitle });
            }}
          />

          <SwitchControl
            label={__("Enable Author", "wc-booster")}
            checked={enableAuthor}
            onChange={(enableAuthor) => {
              setAttributes({ enableAuthor });
            }}
          />

          <SwitchControl
            label={__("Enable Date", "wc-booster")}
            checked={enableDate}
            onChange={(enableDate) => {
              setAttributes({ enableDate });
            }}
          />

          <SwitchControl
            label={__("Enable Comment", "wc-booster")}
            checked={enableComment}
            onChange={(enableComment) => {
              setAttributes({ enableComment });
            }}
          />

          <SwitchControl
            label={__("Enable Rating", "wc-booster")}
            checked={enableRating}
            onChange={(enableRating) => {
              setAttributes({ enableRating });
            }}
          />
        </PanelBody>

        {enableArrow && (
          <PanelBody title={__("Arrow", "wc-booster")} initialOpen={false}>

            <Dimension
            label={__("Arrow Position", "wc-booster")}
            value={arrowPosition}
            defaultValue={def("arrowPosition")}
            onChange={(arrowPosition) => setAttributes({ arrowPosition })}
            />

            <RadiusControl
              label={__("Height", "wc-booster")}
              value={arrowHeight}
              initialPosition={def("arrowHeight")}
              defaultValue={def("arrowHeight")}
              onChange={(arrowHeight) => setAttributes({ arrowHeight })}
            />

            <RadiusControl
              label={__("Width", "wc-booster")}
              value={arrowWidth}
              initialPosition={def("arrowWidth")}
              defaultValue={def("arrowWidth")}
              onChange={(arrowWidth) => setAttributes({ arrowWidth })}
            />

            <RadiusControl
              label={__("Radius", "wc-booster")}
              value={arrowRadius}
              initialPosition={def("arrowRadius")}
              defaultValue={def("arrowRadius")}
              onChange={(arrowRadius) => setAttributes({ arrowRadius })}
            />
          </PanelBody>
        )}
        
        <PanelBody title={__("Spacing", "wc-booster")} initialOpen={false}>
          <Dimension
              label={__("Title Margin", "wc-booster")}
              value={titleMargin}
              defaultValue={def("titleMargin")}
              onChange={(titleMargin) => setAttributes({ titleMargin })}
          />
          <Dimension
              label={__("Item Margin", "wc-booster")}
              value={itemMargin}
              defaultValue={def("itemMargin")}
              onChange={(itemMargin) => setAttributes({ itemMargin })}
          />
          <Dimension
              label={__("Rating Margin", "wc-booster")}
              value={ratingMargin}
              defaultValue={def("ratingMargin")}
              onChange={(ratingMargin) => setAttributes({ ratingMargin })}
          />
          <Dimension
              label={__("Comment Margin", "wc-booster")}
              value={commentMargin}
              defaultValue={def("commentMargin")}
              onChange={(commentMargin) => setAttributes({ commentMargin })}
          />
          <Dimension
              label={__("Content Padding", "wc-booster")}
              value={contentPadding}
              defaultValue={def("contentPadding")}
              onChange={(contentPadding) => setAttributes({ contentPadding })}
          />
        </PanelBody>

        <PanelBody title={__("Image", "wc-booster")} initialOpen={false}>

          <ToggleGroupControl
            value={imageType} 
            onChange={(imageType) => setAttributes({ imageType })}
            isBlock
            >
            <ToggleGroupControlOption value="reviewer" label={__("Reviewer Photo", "wc-booster")} />
            <ToggleGroupControlOption value="product" label={__("Product", "wc-booster")} />
          </ToggleGroupControl>
          <hr />

          <ResponsiveRangeWrapperControl
            defaultValue={metadata.attributes.imageSize.default}
            value={imageSize}
            onChange={(imageSize) => setAttributes({ imageSize })}
            label={__("Image Size", "wc-booster")}
            beforeIcon=""
          />

          <RadiusControl
            label={__("Image Radius", "wc-booster")}
            allowReset={false}
            value={imageRadius}
            onChange={(imageRadius) => setAttributes({ imageRadius })}
          />
        </PanelBody>

        <PanelBody title={__("Typography", "wc-booster")} initialOpen={false}>
        {enableAuthor && (
          <>
            <Typography
            label={__("Title", "wc-booster")}
            value={titleTypo}
            defaultValue={metadata.attributes.titleTypo.default}
            onChange={(titleTypo) => setAttributes({ titleTypo })}
            />
            <hr />
          </>
        )}
        {enableAuthor && (
          <>
            <Typography
            label={__("Author", "wc-booster")}
            value={authorTypo}
            defaultValue={metadata.attributes.authorTypo.default}
            onChange={(authorTypo) => setAttributes({ authorTypo })}
            />
            <hr />
          </>
        )}
        {enableDate && (
          <>
            <Typography
            label={__("Date", "wc-booster")}
            value={dateTypo}
            defaultValue={metadata.attributes.dateTypo.default}
            onChange={(dateTypo) => setAttributes({ dateTypo })}
            />
            <hr />
          </>
        )}
        {enableAuthor && (
          <>
            <Typography
            label={__("Comment", "wc-booster")}
            value={commentTypo}
            defaultValue={metadata.attributes.commentTypo.default}
            onChange={(commentTypo) => setAttributes({ commentTypo })}
            />
          <hr />
        </>
        )}
        {enableRating && (
          <>
            <Typography
              label={__("Rating", "wc-booster")}
              value={ratingTypo}
              defaultValue={metadata.attributes.ratingTypo.default}
              onChange={(ratingTypo) => setAttributes({ ratingTypo })}
            />
            <hr />
          </>
        )}
        </PanelBody>

        <PanelBody title={__("Color", "wc-booster")} initialOpen={false}>
          <PanelColor
            colorSettings={[
               {
                label: __("Background", "wc-booster"),
                value: bgColor,
                onChange: (bgColor) => setAttributes({ bgColor: isUndefined( bgColor ) ? '' : bgColor }),
              },
              enableTitle && {
                label: __("Title", "wc-booster"),
                value: titleColor,
                onChange: (titleColor) => setAttributes({ titleColor }),
              },
              enableAuthor && {
                value: authorColor,
                onChange: (authorColor) => setAttributes({ authorColor }),
                label: __("Author", "wc-booster"),
              },
              enableDate && {
                value: dateColor,
                onChange: (dateColor) => setAttributes({ dateColor }),
                label: __("Date", "wc-booster"),
              },
              enableComment && {
                value: commentColor,
                onChange: (commentColor) => setAttributes({ commentColor }),
                label: __("Comment", "wc-booster"),
              },
              enableRating && {
                value: ratingColor,
                onChange: (ratingColor) => setAttributes({ ratingColor }),
                label: __("Rating", "wc-booster"),
              },
              enableDots && {
                value: dotColor,
                onChange: (dotColor) => setAttributes({ dotColor }),
                label: __("Dots Color", "wc-booster"),
              },
            ]}
          >
            {enableArrow && (
              <>
                <Tab
                  className={`${prefix}-tabbed-color-control`}
                  label={__("Arrow Right Color", "wc-booster")}
                  data={arrowRightSetting}
                />

                <Tab
                  className={`${prefix}-tabbed-color-control`}
                  label={__("Arrow Left Color", "wc-booster")}
                  data={arrowLeftSetting}
                />
              </>
            )}
          </PanelColor>
        </PanelBody>
      </InspectorControls>
    </>
  );
};

export default InspectorPanel;
