import { prefix } from '../constants';
import { ButtonGroup, Button } from '@wordpress/components';

const SizeUnit = ({
  onClick = e => {},
  units,
  activeUnit = 'px',
  className = '',
}) => (
  <ButtonGroup className={`${className} ${prefix}-size-units`}>
    {units.map((unit, i) => {
      if (unit == '') {
        return;
      }
      const className = activeUnit === unit ? 'active' : '';
      return (
        <Button className={className} key={i} onClick={e => onClick(unit)}>
          {unit}
        </Button>
      );
    })}
  </ButtonGroup>
);

export default SizeUnit;
