import "../styles/editor.scss";
import { __ } from "@wordpress/i18n";
import { withInstanceId, compose } from "@wordpress/compose";
import { useEffect, useRef } from "@wordpress/element";
import { withSelect, useSelect } from "@wordpress/data";
import { decodeEntities } from "@wordpress/html-entities";
import {
	RichText,
	BlockControls,
	useBlockProps,
	AlignmentToolbar,
} from "@wordpress/block-editor";

import { Placeholder, Spinner } from "@wordpress/components";

import InspectorPanel from "./inspector";
import { prefix } from "../../../constants";
import {
	isUndefined,
	pickBy,
	getTypoStyle,
	findIndex,
	getDimensionStyle,
	getResponsiveRangeVal,
	wordTrim,
} from "../../../helpers";

import Slider from "react-slick";
import ProductReviewItemLayoutOne from '../layouts/layout-one';
import ProductReviewItemLayoutTwo from '../layouts/layout-two';

const Edit = (props) => {
	const {
		attributes: {
			titleTypo,
			authorTypo,
			dateTypo,
			commentTypo,
			ratingTypo,
			alignment,
			padding,
			authorColor,
			titleColor,
			dateColor,
			commentColor,
			ratingColor,
			arrowRadius,
			arrowWidth,
			arrowHeight,
			buttonBackground,
			buttonTextColor,
			buttonHoverBackground,
			buttonHoverTextColor,
			buttonRadius,
			autoplay,
			pauseOnHover,
			enableArrow,
			enableDots,
			enableTitle,
			enableAuthor,
			enableDate,
			enableComment,
			enableRating,
			dotColor,
			bgColor,
			arrowPosition,
			product_id,
			contentWidth,
			imageRadius,
			imageSize,
			imageType,
			titleMargin,
			itemMargin,
			ratingMargin,
			commentMargin,
			contentPadding,
			ratingPosition,
			excerptLength,
			itemBoxRadius,
			layout
		},
		setAttributes,
		clientId,
	} = props;

	const blockProps = useBlockProps({
		style: {
			...getDimensionStyle("padding", padding),
		},
		className: `${prefix}-product-review-wrapper`,
	});

	const allProduct = useSelect((select) => {
		const { getEntityRecords } = select("core");
		return getEntityRecords("postType", "product", {
			per_page: -1,
			_embed: true,
		});
	});

	useEffect(() => {
		setAttributes({
			block_id: `${prefix}-product-review-block-${blockProps.id}`,
		});
	}, []);

	const sliderRef = useRef(null);

	const item = (post, reviewId) => {

		if (!post || !post["wc_booster_product_reviews"]) {
	        return null; // Return null if post is undefined or does not have wc_booster_product_reviews property
	    }
	    const reviews = post["wc_booster_product_reviews"];

	    const selectedIndex = parseInt(reviewId) - 1;
	    const selectedReview = reviews.filter((review, index) => index === selectedIndex)[0];

	    if (!selectedReview) {
	        return null; // Return null if no review matches the selected reviewId
	    }

		const authorStyle = {
			...getTypoStyle(authorTypo),
			color: authorColor,
		};

		const dateStyle = {
			...getTypoStyle(dateTypo),
			color: dateColor,
		};

		const commentStyle = {
			...getDimensionStyle("margin", commentMargin),
			...getTypoStyle(commentTypo),
			color: commentColor,
		};

		const titleStyle = {
			...getTypoStyle(titleTypo),
			color: titleColor,
			...getDimensionStyle("margin", titleMargin),
		};

		const ratingStyle = {
			...getTypoStyle(ratingTypo),
			color: ratingColor,
			...getDimensionStyle("margin", ratingMargin),
		};

		const productReviewBodyStyle = {
			"width": getResponsiveRangeVal(contentWidth),
			...getDimensionStyle("padding", contentPadding),
		};

		const wrapperStyle = {
			...getDimensionStyle("margin", itemMargin),
			backgroundColor: bgColor,
			borderRadius: itemBoxRadius + "px",
		}

		const productImageStyle = {
			borderRadius: imageRadius + "%",
			height: getResponsiveRangeVal(imageSize),
			width: getResponsiveRangeVal(imageSize),
		}

		const getImgUrl = ({ _embedded }) => {
			let img = "";

			if (
				!isUndefined(_embedded) &&
				!isUndefined(_embedded["wp:featuredmedia"]) &&
				!isUndefined(_embedded["wp:featuredmedia"][0].source_url)
				) {
				img = _embedded["wp:featuredmedia"][0].source_url;
		}

		return img;
		};

	    const productProps = {
	        ...props,
	        post,
	        selectedReview,
	        authorStyle,
	        dateStyle,
	        commentStyle,
	        titleStyle,
	        ratingStyle,
	        productReviewBodyStyle,
	        wrapperStyle,
	        productImageStyle,
	        getImgUrl
	    };
		return (
		    layout === 'one' ? (
		        <ProductReviewItemLayoutOne
		            {...productProps}
		        />
		    ) : (
		        <ProductReviewItemLayoutTwo
		            {...productProps}
		        />
		    )
		);
	};

	const getSliderSettings = (length) => {
		const {
			arrowRightColor,
			arrowLeftColor,
			arrowRightBgColor,
			arrowLeftBgColor,
			slidesToShow,
		} = props.attributes;

		const arrowRightStyle = {
			backgroundColor: arrowRightBgColor,
			color: arrowRightColor,
			borderRadius: arrowRadius + "px",
            height: arrowHeight + "px",
            width: arrowWidth + "px",
            ...getDimensionStyle(
				["top", "right"],
				arrowPosition,
			)

		};

		const arrowLeftStyle = {
			backgroundColor: arrowLeftBgColor,
			color: arrowLeftColor,
			borderRadius: arrowRadius + "px",
            height: arrowHeight + "px",
            width: arrowWidth + "px",
            ...getDimensionStyle(
				["top", "right", "right"],
				arrowPosition,
			)
		};

		const Next = () => {
			return (
				<button
					onClick={() => sliderRef.current.slickNext()}
					type="button"
					className={`${prefix}-next-arrow ${prefix}-slider-arrow`}
					style={arrowRightStyle}
				>
					<i className="fa fa-angle-right"></i>
				</button>
			);
		};

		const Prev = () => {
			return (
				<button
					onClick={() => sliderRef.current.slickPrev()}
					style={arrowLeftStyle}
					type="button"
					className={`${prefix}-prev-arrow ${prefix}-slider-arrow`}
				>
					<i className="fa fa-angle-left"></i>
				</button>
			);
		};

		let show = getResponsiveRangeVal(slidesToShow);
        show = length > show ? show : length;

		const settings = {
			slidesToShow: parseInt(show),
			slidesToScroll: 1,
			autoplay,
			infinite: true,
			pauseOnHover,
			infinite: length <= show ? false : true,
			arrows: enableArrow && length > show,
			dots: enableDots,
			nextArrow: <Next />,
			prevArrow: <Prev />,
			appendDots: (i) => {
				const list = i.map((v, i) => {
					const style =
						v.props.className == "slick-active"
							? {
								backgroundColor: dotColor,
								borderColor: dotColor,
							  }
							: { color: dotColor };
					return (
						<li key={i}>
							<button
								onClick={v.props.children.props.onClick}
								style={style}
							></button>
						</li>
					);
				});

				return <ul>{list}</ul>;
			},
		};

		return settings;
	};

	const hasProducts = Array.isArray(allProduct) && allProduct.length;
	if (!hasProducts) {
		return (
			<>
				<InspectorPanel {...props} />
				<Placeholder
					icon="admin-post"
					label={__("Product Review", "wc-booster")}
				>
					{!Array.isArray(allProduct) ? (
						<Spinner />
					) : (
						__("No product found.", "wc-booster")
					)}
				</Placeholder>
			</>
		);
	} else {
		let selectedPages = [];
		if (typeof product_id === 'string' && product_id.trim() !== '') {
		    selectedPages = JSON.parse(product_id);
		}

		const slidePages = selectedPages
			.filter((p) => p.id && p.id !== 0)
			.map((p, i) => {
				const _i = findIndex(allProduct, { id: parseInt(p.id) });
				let revewId = p.review_id ? p.review_id : ""; 
				return <div key={i}>{item(allProduct[_i], revewId)}</div>;
		});

		const settings = getSliderSettings(selectedPages.length);

		return (
			<>
				<BlockControls>
					<AlignmentToolbar
						value={alignment}
						onChange={(alignment) => setAttributes({ alignment })}
					/>
				</BlockControls>
				<section {...blockProps}>

					{slidePages.length > 0 ? (
						<Slider
							className={`${prefix}-align-${alignment}`}
							ref={sliderRef}
							{...settings}
						>
						{slidePages}
						</Slider>
						) : (
						<Placeholder
							icon="admin-page"
							label={__("Please select a product", "wc-booster")}
						></Placeholder>
						)}
				</section>
				<InspectorPanel {...props} allProduct={allProduct} />
			</>
		);
	}
};

export default Edit;

