import ResponsiveViewsIcons from "./responsive-view-icons";
import SizeUnit from "./size-unit";
import { defaultResponsiveRange, prefix, sizeUnit } from "../constants";
import { merge, getPreviewDevice } from "../helpers";
import { FontSizeControl } from "../custom-control";

export default class ResponsiveRangeControl extends wp.element.Component {
	handleChange = (property, value) => {
		const oldData = this.getAttributesValues();
		const { onChange, name } = this.props;
		const activeView = getPreviewDevice();

		const obj = {
			[property]: property === "activeUnit" ? value : { [activeView]: value },
		};
		const newData = merge({}, oldData, obj);
		onChange(name, newData);
	};

	getAttributesValues = () => {
		const { value } = this.props;
		return value
			? merge({}, defaultResponsiveRange, value)
			: defaultResponsiveRange;
	};

	render() {
		const {
			label,
			responsiveViews,
			step = "1",
			beforeIcon = "editor-textcolor",
			onChange,
		} = this.props;

		const oldData = this.getAttributesValues();

		const {
			activeUnit,
			units = sizeUnit,
			values,
			range: { min, max },
		} = oldData;

		const activeView = getPreviewDevice();

		return (
			<div className={`${prefix}-responsive-range-control`}>
				<div className={`${prefix}-label-section`}>
					<label className="components-base-control__label">{label}</label>

					<ResponsiveViewsIcons
						responsiveViews={responsiveViews}
						onClick={(view) => {
							// we need to trigger this to re render the dom
							onChange("", oldData);
						}}
					/>

					<SizeUnit
						units={units}
						activeUnit={activeUnit}
						onClick={(v) => this.handleChange("activeUnit", v)}
					/>
				</div>

				<FontSizeControl
					step={step}
					min={min}
					max={max}
					beforeIcon={beforeIcon}
					label={false}
					allowReset={false}
					value={values[activeView]}
					onChange={(v) => this.handleChange("values", v)}
				/>
			</div>
		);
	}
}
