import { useState } from "@wordpress/element";
import { useSettings } from "@wordpress/block-editor";
import { Button, SelectControl } from "@wordpress/components";
import { __ } from "@wordpress/i18n";
import { merge } from "../helpers";
import ResponsiveRangeControl from "../components/responsive-range-control";

import {
	prefix,
	defaultResponsiveViews,
	defaultFontFamily,
	defaultFontWeight,
	defaultTextTransform,
} from "../constants";

const Typography = (props) => {
	const {
		defaultValue,
		value,
		onChange,
		label = __("Typography", "wc-booster"),
	} = props;
	const [showAdvanceOptions, setShowAdvanceOptions] = useState(false);

	const [blockLevelFontFamilies] = useSettings("typography.fontFamilies");

	const toggleAdvanceOptions = () => {
		setShowAdvanceOptions(!showAdvanceOptions);
	};

	const reset = () => {
		defaultValue && onChange(defaultValue);
	};

	const handleChange = (k, v) => {
		const value = getValue();
		const data = merge({}, value, { [k]: v });
		onChange(data);
	};

	const getValue = () => {
		return value ? value : {};
	};

	let typography = getValue();
	const { lineHeight, fontFamily, fontWeight, fontSize, textTransform } =
		typography;

	return (
		<div className={`${prefix}-typegraphy-options`}>
			<div className={`${prefix}-typography-options-actions`}>
				<label className="components-base-control__label"> {label} </label>
				<div>
					<Button
						onClick={toggleAdvanceOptions}
						className="is-button is-default is-small"
					>
						<span className="dashicons dashicons-admin-tools"></span>
					</Button>
					<Button
						onClick={reset}
						className={`is-button is-default is-small ${prefix}-btn-reset`}
					>
						{__("Reset", "wc-booster")}
					</Button>
				</div>
			</div>

			{showAdvanceOptions && (
				<div className={`${prefix}-typography-advanced`}>
					<ResponsiveRangeControl
						step="0.1"
						name="fontSize"
						responsiveViews={defaultResponsiveViews}
						value={fontSize}
						onChange={handleChange}
						label={__("Font Size", "wc-booster")}
					/>

					<SelectControl
						label={__("Font Family", "wc-booster")}
						value={fontFamily}
						onChange={(f) => {
							handleChange("fontFamily", f);
						}}
					>
						{blockLevelFontFamilies && blockLevelFontFamilies.custom && blockLevelFontFamilies.custom.length && (
							<optgroup label="Custom">
								{blockLevelFontFamilies.custom.map((v, i) => {
									return (
										<option key={i} value={v.fontFamily}>
											{v.name}
										</option>
									);
								})}
							</optgroup>
						)}

						{blockLevelFontFamilies && blockLevelFontFamilies.theme && blockLevelFontFamilies.theme.length && (
							<optgroup label="Theme">
								{blockLevelFontFamilies.theme.map((v, i) => {
									return (
										<option key={i} value={v.fontFamily}>
											{v.name}
										</option>
									);
								})}
							</optgroup>
						)}

						<optgroup label="Default">
							{defaultFontFamily.map((v, i) => {
								return (
									<option key={i} value={v.value}>
										{v.label}
									</option>
								);
							})}
						</optgroup>
					</SelectControl>

					<SelectControl
						label={__("Text transform", "wc-booster")}
						value={textTransform}
						onChange={(t) => handleChange("textTransform", t)}
						options={defaultTextTransform}
					/>

					<SelectControl
						label={__("Font Weight", "wc-booster")}
						value={fontWeight}
						onChange={(fw) => {
							handleChange("fontWeight", fw);
						}}
						options={defaultFontWeight}
					/>

					<ResponsiveRangeControl
						step="0.1"
						name="lineHeight"
						value={lineHeight}
						onChange={handleChange}
						responsiveViews={defaultResponsiveViews}
						label={__("Line Height", "wc-booster")}
					/>
				</div>
			)}
		</div>
	);
};
export default Typography;
